$dark-text-color: #22313f;
$white-color: #ECECEC;
$primary-color: #2574a9;


html, body{
    color: $dark-text-color;
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
    overflow-x: hidden;
    padding: 0;
    margin: 0;
}

table{
    border-collapse: collapse;
}



